//导入type里面的文件
import { UPDATE_AUTHORITY_INFO } from "./type";
export default {
    [UPDATE_AUTHORITY_INFO]: function (state, payload) {
        for (let key in payload) {
            //判断键名是否都一样
            if (Object.keys(state).includes(key)) {
                state[key] = payload[key]
            }
        }
    }
}
