import axios from 'axios'
import router from "@/router";
import { Message } from "element-ui";
//全局
// axios.defaults.baseURL = 'http://localhost:3000/admin';
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
//局部 可以跟多个后端对接
// const request = axios.create()
// // request.defaults.baseURL = 'http://localhost:3000/admin';
// request.defaults.baseURL = 'http://43.138.81.225:5566/admin';
const request = axios.create({
    baseURL: 'http://43.138.81.225:5566/admin'  // 当前实例的默认请求位置
});


// request.defaults.headers.common['Authorization'] = AUTH_TOKEN;
request.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// 添加请求拦截器
request.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    //返回token
    let str = localStorage.getItem('info')

    if (str) {
        let data = JSON.parse(str)
        let token = data.token;
        console.log(token)
        if (token) {
            config.headers["token"] = token;
        }
    }
    return config
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});

// 添加响应拦截器


request.interceptors.response.use(function (response) {
    //处理错误的token
    console.log('响应拦截', response)

    let res = response.data
    //解构
    let { code, message } = res
    //错误
    // let message = response.data.message
    // console.log(message)
    // console.log(res)
    //说明请求成功 直接把数据响应出去
    let isSuccess = code == 200; //成功的
    let isTokenBreack = code == 10119//token失效的我呢提
    if (isSuccess) {
        return response.data
    } else if (isTokenBreack) {
        Message.error("token过期,请重新登录");
        //跳到之前的页面
        router.push({ name: 'login', query: { ReturnUrl: router.currentRoute.fullPath } })
        //返回错误的数据
        let error = new Error(message);
        return Promise.reject(error);
    }


    else {
        // 响应成功 -> 操作成功   => (登录 -> 登录失败(不存在该用户  密码有误))
        var error = new Error(message);
        return Promise.reject(error)
    }
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    // return response;
}, function (error) {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    return Promise.reject(error);
});
export default request