import request from "@/utils/request";

export const getProListAPI = (params) => request.get("/pro/list", {
    params: params,
});

// 更新秒杀
export const updateIsseckillAPI = (params) => request.post("/pro/updateFlag", Object.assign({ type: "isseckill" }, params));
export const updateIsrecommendAPI = (params) => request.post("/pro/updateFlag", {
    type: "isrecommend",
    ...params,
});
// 详情   get请求直接传递一个对象
export const getProDetailAPI = (params) => request.get('/pro/detail', { params })
//筛选商品
export const searchProAPI = (params) => request.post('/pro/searchPro', params)

//获取商品的分类

export const getCategoryAPI = (params) => request.get('/pro/getCategory', { params })



