<template>
  <div class="bannerList">
        <el-table border :data="list" style="width: 100%">
            <el-table-column prop="bannerid" label="编号" width="200">
            </el-table-column>
            <el-table-column  label="图片" width="200px" >
                <!-- slotScope 作用域插槽 -> 组件内向组件传递的数据  => {row:当前行的数据} -->
                <template #default="slotScope">
                    <img class="img" :src="slotScope.row.img" alt="">
                </template>
            </el-table-column>
            <el-table-column  prop="alt" label="图片提示" width="200px" >
            </el-table-column>
            <el-table-column  prop="link" label="图片链接" width="200px" >
            </el-table-column>
            <el-table-column prop="flag" label="公开">
                <template #default="slotScope">
                    <el-switch
                    v-model="slotScope.row.flag"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    @change="changeStateHandler(slotScope.row.bannerid,$event)">
                    </el-switch>
                </template>

            </el-table-column>
            <el-table-column label="操作" @click="delAll()">
                <template  #default="slotScope">
                    <el-button @click="deleteBannerById(slotScope.row.bannerid)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import {getBannerListApi, delBannerApi, delOneBannerApi,updateBannerFlagAPI,} from '@/api/banner'
// import {delApi} from '@/api/del'
    console.log(delOneBannerApi,delBannerApi,updateBannerFlagAPI,)
export default {
    //拿到后端的数据
    name:'BannerListView',
    data(){
    return {
    list:null,
    }
    },
    methods: {
        //拿到后端的数据
        async getBannerList() {
            try {
                var res = await getBannerListApi();
                console.log("请求成功", res);
            //    let {data}=res
            //    console.log(data[0])
            //    console.log(data)
                this.list = res.data;
               
            } catch (err) {
                console.log('失败',err);
            }
        },
       //更新的
       async changeStateHandler(bannerid,flag){
            console.log(bannerid,flag);
            try {
                var res = await updateBannerFlagAPI({
                    bannerid,
                    flag
                });
                console.log("请求成功", res);
                this.$message.success("状态更新成功");
            } catch (err) {
                console.log("失败", err);
                this.$message.err(err.message);
            }

        },
     //删除的函数
      async  deleteBannerById(bannerid){
            console.log(bannerid)
         try{
            //全部删除的
        let res=  await delOneBannerApi({
            bannerid 
            });
            console.log(res)
            this.$message.success("状态更新成功");
            //更新视图
            var index = this.list.findIndex(v=>v.bannerid == bannerid);
                console.log(index);
                this.list.splice(index,1);
         }catch (err) {
                console.log('失败',err);
            }
        },
        //删除全部的但是不知道绑定哪里触发
        async delAll(){
            console.log(111111)
            try {
                await delBannerApi()
            }catch(err){
                    console.log(err)
            }
        }
      
    },
    mounted() {
        this.getBannerList()
       
    },
    
}
</script>

<style lang="scss" scoped>
        .bannerList .img{
        width: 100%;
    }
</style>