// import request from "@/utils/request";
import request from "@/utils/request";
export const getBannerListApi = (params) => request.get("/banner/list", { params: params, })
//获取添加图片的接口        因为那边传的就是对象 所有可以直接接受
export const addBannerApi = (params) => request.post('/banner/add', params)
//删除的接口
export const delBannerApi = (params) => request.get('/banner/removeAll', { params: params })
//单个删除                  因为get低二个参数是对象 那边传的是字符串
//所以这边要用对象包起来                                           相当去简写 params：params
//只不过简写了 那边穿过来就是 params:图片的id
export const delOneBannerApi = (params) => request.get('/banner/delete', { params })
//试图更新的接口
export const updateBannerFlagAPI = (params) => request.post('/banner/updateFlag', { params: params }, {})







