import request from "@/utils/request";

// 管理员登录
export const adminLoginApi = (params) => request.post("/admin/login", params);

export const getAdminListAPI = (params) => request.get("/admin/list", {
    params
});

export const getAdminDetailAPI = (params) => request.get("/admin/detail", {
    params
});

export const addAdminAPI = (params) => request.post("/admin/add", params);
export const delAdminAPI = (params) => request.post("/admin/delete", params);
export const updateAdminAPI = (params) => request.post("/admin/update", params);





