<template>
  <div>
    <!-- 绑定点击事件 -->
    <el-dropdown trigger="click" @command="itemChange">
      <span class="el-dropdown-link">
        <span>{{ $store.state.adminname }}</span
        ><i class="el-icon-platform-eleme el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <!-- 绑定点击事件 -->
        <el-dropdown-item :command="1">查看</el-dropdown-item>
        <el-dropdown-item :command="2">新增</el-dropdown-item>
        <el-dropdown-item :command="3">删除</el-dropdown-item>
        <el-dropdown-item :command="4">退出</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "HeaderLayout",
  methods: {
    // 调用这个删除的方法
    ...mapActions(["exitAndUpdateAuthorityInfo"]),
    //调用这个点击事件
    itemChange(commandValue) {
      if (commandValue === 4) {
        //要是点击的是退出就退出
        // 1. 清除本地数据  vuex存储数据
        // 2. 回到登录页
        this.exitAndUpdateAuthorityInfo();
        this.$router.push("/login");
      }
    },
  },
};
</script>

