    <el-menu :router="true" unique-opened :default-active="$route.path">
        <template v-for="(menu,index) in menus" >
             <el-menu-item :index="menu.path" :key="index" v-if="!menu.children" >
                <i class="el-icon-platform-eleme"></i>{{menu.label}}
             </el-menu-item>
             <el-submenu :index="menu.path"  :key="index" v-else>
                <template slot="title"><i class="el-icon-menu"></i>{{menu.label}}</template>
                <el-menu-item v-for="(subMenu,index) in menu.children" :key="index" :index="subMenu.path">{{ subMenu.label }}</el-menu-item>
               
            </el-submenu>
        </template>
     
       
    </el-menu>

</template>

<script>
import { menus } from '@/router/routes';
export default {

    name:"AsideLayout",
    data(){
            return {
                menus:menus,
            }
    }
}
