//导入同步的 到时候直接调用异步的就可以了
import { adminLoginApi } from '@/api/admin'
import { UPDATE_AUTHORITY_INFO, AUTHORITY_INIT_VALUE } from './type'
export default {
    //在写一个函数吧东西永久存到state里面
    getAuthorityInfo: function ({ commit }) {
        //获取到 localStorage的值

        let str = localStorage.getItem('info')
        // console.log(localStorage.getItem('info'))

        if (str) {
            // console.log(11)
            //假如 localStorage 有值我们就取出来在转换为对象
            let data = JSON.parse(str)
            // console.log(data)
            console.log(data.adminname)
            //继续存到state里面
            commit(UPDATE_AUTHORITY_INFO, data)
            // console.log(data)
            return data
        } else {
            return ''
        }
    },

    //直接去调用mutantions里面的结果
    loginAndUpdateAuthorityInfo({ commit }, payload) {
        //接受结果
        return adminLoginApi(payload).then(res => {
            //通过了一次拦截  所以返回的是data里面的data
            //res.data就是第二层
            //取到data就是登录的所有信息
            let data = res.data
            console.log(data)
            //使用commit调用  将所有的登入信息给state
            commit(UPDATE_AUTHORITY_INFO, data)
            //登入成功存到本地存储
            localStorage.setItem('info', JSON.stringify(data))
            return data
        })
    },
    //退出登入
    exitAndUpdateAuthorityInfo({ commit }) {
        //删除本地存储的数据
        localStorage.removeItem('info');
        //将vuex里面的数据给清除
        commit(UPDATE_AUTHORITY_INFO, AUTHORITY_INIT_VALUE)
    }
}